import { Component, OnInit, Output, EventEmitter, Pipe, PipeTransform } from '@angular/core';
import { GlobalEventsManager } from '../../core/global-events-manager.service';
import { BooksService } from '../../core/books.service';
import { Book } from '../../core/book';

@Pipe({
  name: 'searchFilter'
})
export class SearchFilter implements PipeTransform {
  transform(items: any[], criteria: any): any {
    return items.filter(item => {
      return (item.title.toLowerCase().includes(criteria.toLowerCase())) || (this.containsAuthor(item.author, criteria.toLowerCase()));
    });
  }
  containsAuthor(authors, authorCriteria) {
    let found = false;
    authors.forEach(author => {
      found = author.toLowerCase().includes(authorCriteria);
    });
    return found;
  }
}


@Component({
  selector: 'atl-all-books',
  templateUrl: './all-books.component.html',
  styleUrls: ['./all-books.component.css']
})
export class AllBooksComponent implements OnInit {
  filterAllActive = true;
  filterIssuedActive = false;
  books: Book[];

  constructor(private globalEventsManager: GlobalEventsManager, private bookService: BooksService) { }

  ngOnInit() {
    this.books = this.bookService.getAllBooksAdmin();
  }

  onAddBookClicked(): void {
    this.globalEventsManager.addBookClicked(true);
  }

  onIssuedFilter(): void {
    this.books = this.books.filter(book => {
      return book.issuedBy.length > 0;
    });
    this.filterAllActive = false;
    this.filterIssuedActive = true;
  }
  onAllFilter(): void {
    this.books = this.bookService.getAllBooksAdmin();
    this.filterAllActive = true;
    this.filterIssuedActive = false;
  }

}



